	module.exports = {"data":{"contentfulTheorie":{"titre":"Les gènes Drumel","texte":{"childMarkdownRemark":{"html":"<p>Les gènes Drumel est le nom du groupe de gène associé à définir si un individus a, aura ou n’aura pas de pouvoirs durant sa vie. Ce groupe de gènes porte le nom de l’individus qui a découvert et recherchés ses gènes, Darakei Drumel.</p>\n<h3>Types de gènes</h3>\n<ul>\n<li><strong>Gène inactif</strong>: Les gènes inactifs est le type de gène le plus commun, il est forcément associé aux humains puisque avoir des gènes inactifs implique qu’on ne possède aucun pouvoir. Il y a 99.85% de la population mondiale qui possède ce type de gènes.</li>\n<li><strong>Gène latent</strong>: les gènes latents est le type de gène qui signifie une transition entre le statut d’humain vers le statut de nouvel humain. Il est possible que le gène devient actif durant la vie de l’individus ou qu’un de ses enfants possède les gènes actifs, en autre cas les gènes restent latent. Il y a 0.14% de la population mondiale qui possède ce type de gènes.</li>\n<li><strong>Gène actif</strong>: les gènes actifs implique que l’individu possède un pouvoir, qu’il soit maîtrisé ou non. Dès que le gène change d’état, l’individus devient un nouvel humain. Il y a 0.01% de la population mondiale qui possède ce type de gènes.</li>\n<li><strong>Gène artificiel</strong>: les gènes artificiels implique que l’individu possède un pouvoir obtenu de manière artificielle.</li>\n</ul>"}}}},"pathContext":{"slug":"les-genes-drumel"}}

